import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './App.css';

function App() {
    // конструктор главного компонента
    const [people, setPeople] = useState([]);

    useEffect(() => {
        axios({
            method: 'GET',
            url: 'http://127.0.0.1:8000/api/test-api/'
        }).then((response) => {
            setPeople(response.data);
        });
    }, []);

    return (
        <div className="App">
            <h1>Hello from Venus</h1>
            <hr/>
            <h3>Site users List:</h3>
            <div className = 'people_box'>
                <ol>
                    {people.map(p => (
                        <li key={p.id}>{p.first_name} {p.last_name} - {p.age} years</li>
                    ))}
                </ol>
            </div>
        </div>
  );
}

export default App;
